section{color:#3f536e;font-size:14px
}
section .demo-block{padding:32px;border:1px solid #e2ecf4;border-radius:4px 4px 0 0;background-color:#fff;margin-top:16px
}
section hr{margin:1.2em 0;border:0;border-bottom:1px solid #C5D9E8
}
section p{padding:0;line-height:1.7;color:#3f536e;font-size:14px
}
section h1{font-size:24px
}
section h1,section h2,section h3,section h4,section h5{margin:16px 0;line-height:1.5
}
section h2{font-size:18px
}
section ul{margin:16px 0;padding-left:24px;list-style:disc
}
section ul li{line-height:1.8
}
section pre{margin:16px 0;padding:16px;background-color:#F7F7F7;overflow:auto
}
section pre code{padding:0;font-size:1em;border:none;border-radius:0;background-color:transparent
}
section code{padding:.3em .5em;font-size:.9em;vertical-align:middle;border:1px solid #ECECEC;border-radius:2px;background-color:#F7F7F7
}
section code.code_inline{padding:0 .5em
}
section img{vertical-align:middle
}
section p+p{margin-top:8px
}
section a{color:#6190e8
}
section table.table{display:block;width:100%;background-color:#fff;color:#5e6d82;font-size:14px;border-collapse:collapse;overflow:auto;margin-bottom:50px
}
section table.table th{border-top:1px solid #f3f7fa;background-color:#FAFBFC;border-bottom:1px solid #f3f7fa;padding:10px 8px
}
section table.table td{border-top:1px solid #f3f7fa;border-bottom:1px solid #f3f7fa;padding:10px 8px
}
body,ol,ul,h1,h2,h3,h4,h5,h6,p,th,td,dl,dd,form,fieldset,legend,input,textarea,select{margin:0;padding:0
}
body{font:12px;background:#fff;-webkit-text-size-adjust:100%
}
a{color:#2d374b;text-decoration:none
}
a:hover{color:#cd0200;text-decoration:underline
}
em{font-style:normal
}
li{list-style:none
}
img{border:0;vertical-align:middle
}
table{border-collapse:collapse;border-spacing:0
}
p{word-wrap:break-word
}
.demo-container{-webkit-transition:max-height .3s ease;transition:max-height .3s ease;overflow:hidden
}
.docs-demo{width:100%;height:auto;-webkit-box-sizing:border-box;box-sizing:border-box;font-size:14px;background-color:#F7F7F7;border:1px solid #e2ecf4;border-top:none
}
.docs-demo pre code{font-family:Consolas,Menlo,Courier,monospace;line-height:22px;border:none
}
.docs-trans{width:100%;text-align:center;display:inline-block;color:#C5D9E8;font-size:12px;padding:10px 0;background-color:#FAFBFC
}
.docs-demo__code,.highlight-wrapper,.docs-demo__meta{padding:0 20px;overflow-y:auto
}
.docs-demo__code{border-bottom:1px solid #eee
}
.docs-demo.docs-demo--expand .docs-demo__meta{border-bottom:1px dashed #e9e9e9
}
.docs-demo.docs-demo--expand .docs-demo__triangle{-webkit-transform:rotate(180deg);transform:rotate(180deg)
}
.highlight-wrapper{display:none
}
.highlight-wrapper p,.highlight-wrapper pre{margin:0
}
.highlight-wrapper .hljs{padding:0
}
.docs-demo.docs-demo--expand .highlight-wrapper{display:block
}
.docs-demo__code__mobi{height:620px;margin:20px 0
}
.docs-demo__code__mobi__header{border-radius:4px 4px 0 0;background:-webkit-gradient(linear, left top, left bottom, from(rgba(55,55,55,0.98)), to(#545456));background:linear-gradient(rgba(55,55,55,0.98), #545456);text-align:center;padding:8px
}
.docs-demo__code__mobi__header img{width:100%
}
.docs-demo__code__mobi__header .url-box{height:28px;line-height:28px;color:#fff;padding:0 3px;background-color:#a2a2a2;margin:10px auto 0;border-radius:4px;white-space:nowrap;overflow-x:auto
}
.docs-demo__code__mobi__content iframe{width:100%;border:0;height:548px
}
